footer {
  background-color: #363f48;
  color: $white;
  padding-top: 60px;
  padding-bottom: 40px;
  a {
    color: $white;
    position: relative;
    text-transform: uppercase;
    &:hover {
      color: $white;
      text-decoration: none;
      &:after {
        transform: scaleX(1);
      }
    }
    &:after {
      content: ' ';
      position: absolute;
      bottom: -1px;
      left: 0px;
      height: 1px;
      width: 100%;
      transform-origin: left;
      transform: scaleX(0);
      background-color: $white;
      transition: transform .3s ease-out;
    }
  }
  h2 {
    font-weight: 600;
    text-transform: uppercase;
  }
}

ul.footer-links {
  i.fa-chevron-right {
    color: $blue;
    transition: color .3s ease-out;
  }
  li {
    margin-bottom: 6px;
    &:hover > i {
      color:$white;
    }
  }
  li > a {
    position: relative;
    text-transform: uppercase;
    &:hover {
      color: $white;
      text-decoration: none;
      &:after {
        transform: scaleX(1);
      }
    }
    &:after {
      content: ' ';
      position: absolute;
      bottom: -1px;
      left: 0px;
      height: 1px;
      width: 100%;
      transform-origin: left;
      transform: scaleX(0);
      background-color: $white;
      transition: transform .3s ease-out;
    }
  }
  &:hover a {
    color: darken($white, 50%);
  }

}

.row-footer {
  margin-bottom: 30px;
}

.subfooter {
  height: 69px;
  background-color: $gray-dark;
  color: #575d63;
  padding-top: 28px;
  p {
    margin: 0;
    font-size: 13px;
  }
  a {
    color: lighten($blue, 5%);
  }
}
